export default {
	data() {
		return {
			showBack: true,
			keyWord: '',
			storeId:"",
			goodsList: [],
			historyList: [],
			noData: false,
			// 上拉加载参数
			pageSize: 6,
			currentPage: 1,
			// 上拉加载参数
			loadMoreText: '加载更多...',
			loadMoreFlag:false,
			
		};
	},
	onReachBottom() {
		this.loadMoreFlag=true;
		setTimeout(() => {
			this.getSearchGoods();
		}, 500);
	},
	onLoad(e) {
		console.log(e);
		this.keyWord = e.keyWord;
		this.storeId=e.storeId;
		this.getSearchGoods();
		this.getHistorySearch();
	},
	methods: {
		//获取历史搜索记录
		getHistorySearch() {
			let that = this;
			uni.getStorage({
				key: 'keyWord',
				success: res => {
					that.historyList = res.data.reverse();
				}
			});
		},
		//添加历史搜索记录
		setSearchHistory() {
			let that = this;
			if (that.keyWord == '') {
				that.Tips({ title: '请输入关键字' });
				return;
			}
			//判断关键字是否存在历史记录之中
			if (that.historyList.indexOf(that.keyWord) == -1) {
				that.historyList.reverse();
				that.historyList.push(that.keyWord);
				uni.setStorage({
					key: 'keyWord',
					data: that.historyList
				});
				that.historyList.reverse();
			}
			that.goodsList = [];
			that.currentPage=1;
			console.log(that.keyWord);
			that.getSearchGoods();
		},
		//获取搜索的商品
		getSearchGoods() {
			let that = this;
			console.log("搜索");
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'get_product_list',
					q: {
						cid: '',
						mid:that.storeId,
						keyword: that.keyWord,
						priceOrder: '',
						salesOrder: '',
						news: '',
						page: that.currentPage,
						limit: that.pageSize
					}
				}),
				function(res) {
					that.goodsList = that.goodsList.concat(res.data);
					console.log(that.goodsList);
					that.currentPage++;
					if(res.data.length==0){
						that.loadMoreText="没有更多数据了！"
					}
					if (that.goodsList.length == 0) {
						that.noData = true;
					} else {
						that.noData = false;
					}
					setTimeout(()=>{
						that.loadMoreFlag=false;
					},1000);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		//返回上一页
		back() {
			uni.navigateBack();
		},
		detail(id) {
			uni.navigateTo({
				url: '/pages/list/goode_details/goode_details?id='+id
			});
		}
	}
};
